import Fetch, { requestOption } from '../index'

const fetch = new Fetch();

export const getRolesList = (limit: number, offset?: number, skip?: number, filter?: any, order?: any) => {
    let option: requestOption = {
        method: 'GET'
    }
    console.log(filter);

    return fetch.call(option, `account-roles?${filter}filter[limit]=${limit}&filter[skip]=${skip}&filter[offset]=${offset}${order}`)
}


export const getRoleData = (id: any) => {
    let option: requestOption = {
        method: 'GET'
    }

    // return fetch.call(option, `account-roles?filter={"where":{"_id":"${id}"}}`)
    return fetch.call(option, `roles/${id}`)
}

export const searchRoleData = (query: any) => {
    let option: requestOption = {
        method: 'GET'
    }

    return fetch.call(option, `account-roles/search?query=${query}`)
}

export const getRbacList = () => {
    let option: requestOption = {
        method: "GET"
    }

    return fetch.call(option, `rbac?filter[offset]=0&filter[skip]=0`)
}


export const getRbacCount = (module: string) => {
    let option: requestOption = {
        method: "GET"
    }

    return fetch.call(option, `role-rbac/count?where={"module":"${module}"}`)
}

export const createRoleRbac = (data: any) => {
    let option: requestOption = {
        method: "POST",
        body: JSON.stringify(data)
    }
    console.log(option)
    return fetch.call(option, `role-rbac`, true)
    // return fetch.call(option, `account-roles?filter={"where":{"_id":"${id}"}}`)
}

export const updateRoleRbac = (id: string, data: any) => {
    let option: requestOption = {
        method: "PUT",
        body: JSON.stringify(data)
    }

    return fetch.call(option, `role-rbac/${id}`, true)
}

export const getAllRoles = (limit: number) => {

    let option: requestOption = {
        method: 'GET'
    }

    return fetch.call(option, `roles?filter[limit]=${limit}`)
}

export const getAllGroups = () => {

    let option: requestOption = {
        method: 'GET'
    }

    return fetch.call(option, `group-list`)
}