import Fetch, { requestOption } from '../index'

const fetch = new Fetch();

export const getUserList = (
    limit: number,
    offset: number,
    where?: any,
    order?: any
) => {

    let option: requestOption = {
        method: 'GET'
    }

    let filter: any = {
        limit,
        offset
    };

    if (order && order !== '') {
        filter.order = order;
    }

    if (where && Object.keys(where).length > 0) {
        filter.where = where;
    }


    return fetch.call(option, `accounts/list?filter=${JSON.stringify(filter)}`)
}

export const searchUser = (
    limit: number,
    offset: number,
    query: any,
    where?: any,
    order?: any
) => {

    let option: requestOption = {
        method: 'GET'
    }

    let filter: any = {
        limit,
        offset,
        query: query
    };

    if (order && order !== '') {
        filter.order = order;
    }

    if (where && Object.keys(where).length > 0) {
        filter.where = where;
    }

    return fetch.call(option, `accounts/search?${new URLSearchParams(filter).toString()}`)
}

export const getUserDetails = (id: string) => {

    let option: requestOption = {
        method: 'GET'
    }

    return fetch.call(option, `accounts/list/${id}`)
}

export const updateUserDetails = (id, body) => {
    let option: requestOption = {
        method: 'PUT',
        body: JSON.stringify(body)
    }

    return fetch.call(option, `user/${id}`, true)
}

export const changePassword = (data, userId) => {

    let option: requestOption = {
        method: 'PUT',
        body: JSON.stringify(data)
    }
    console.log(userId)

    return fetch.fetchAccessToken().then(() => fetch.call(option, `user/password`, true))
}

export const addNewUser = (data: any) => {
    let option: requestOption = {
        method: 'POST',
        body: JSON.stringify(data)
    }

    return fetch.call(option, `users`, true)
}


export const getProfile = () => {
    let option: requestOption = {
        method: 'GET'
    }
    
    return fetch.fetchAccessToken().then(() => fetch.call(option, `profile`,true))
}

export const getDepartments = () => {
    let option: requestOption = {
        method: 'GET'
    }
    
    return fetch.call(option, `departments/list`, true)
}


export const resetPassword = (username: string) => {
    let option: requestOption = {
        method: 'POST'
    }

    return fetch.call(option, `password/reset/${username}`);
}

