interface userData {
  username: string
  first_name: string
  middle_name: string
  last_name: string
  email: string
  expiry_date: any
  location: string
  job_title: string
  department: string
  profile_pic: string
  employee_id: string
  main_module: string
  main_role: string
  section: string
  effectivity_date: any
  roles: any
  group: any
  reports_to: any
}

interface rolesList {
  rows: any[],
  options: any[]
}

interface groupList {
  rows: any[]
}

